@import '../../sass/mixins'

.gallery
    position: relative
    width: 100%
    height: 100%
    display: grid
    grid-template-columns: repeat(14, 1fr)
    grid-template-rows: repeat(6, 1fr)
    grid-gap: var(--gapBig)
    z-index: 999

    @media (orientation: portrait)
        grid-template-columns: repeat(3, 1fr)
        grid-template-rows: repeat(5, 1fr)

    &--enlarged_mode
        grid-template-columns: 5fr 5fr
        grid-template-rows: 1fr

        @media (orientation: portrait)
            grid-template-columns: 1fr
            grid-template-rows: 6fr 4fr

    &__miniatures
        height: 100%
        width: 100%
        display: grid
        grid-template-columns: repeat(4, 1fr)
        grid-gap: var(--gapBig)
        @media (orientation: portrait)
            display: none

    &__enlarged_image
        position: relative
        grid-row: span 1
        grid-column: span 1
        @media (orientation: portrait)
            position: fixed
            top: 0
            left: 0
            width: 100vw
            height: 100vh

    &__next_image, &__previous_image, &__close_image
        height: 4.6vh
        width: 4.6vh
        position: absolute
        z-index: 9999
        cursor: pointer
        filter: grayscale(1)
        transition: .2s

    &__next_image
        right: 2%
        top: 50%
        transform: translateY(-50%) rotate(135deg)
        @media (orientation: portrait)
            right: 5%
        &:hover
            filter: grayscale(0) drop-shadow(4px -4px 4px rgba(0, 0, 0, 0.4))
            transform: translateY(-50%) rotate(135deg) scale(1.2)
        &:active
            filter: grayscale(0) drop-shadow(3px -3px 3px rgba(0, 0, 0, 0.5))
            transform: translateY(-50%) rotate(135deg) scale(1.15)

    &__previous_image
        left: 2%
        top: 50%
        transform: translateY(-50%) rotate(-45deg)
        @media (orientation: portrait)
            left: 5%
        &:hover
            filter: grayscale(0) drop-shadow(-4px 4px 4px rgba(0, 0, 0, 0.4))
            transform: translateY(-50%) rotate(-45deg) scale(1.2)
        &:active
            filter: grayscale(0) drop-shadow(-3px 3px 3px rgba(0, 0, 0, 0.5))
            transform: translateY(-50%) rotate(-45deg) scale(1.15)

    &__close_image
        right: 1%
        top: 1%
        height: 6vh
        width: 6vh
        transform-origin: 50% 50%
        transform: translateY(0%) scale(1)
        &:hover
            filter: grayscale(0) drop-shadow(4px 4px 4px rgba(0, 0, 0, 0.4))
            transform: translateY(0%) scale(1.2)
        &:active
            filter: grayscale(0) drop-shadow(3px 3px 3px rgba(0, 0, 0, 0.5))
            transform: translateY(0%) scale(1.15)

    &__arrow_first
        position: absolute
        top: 0
        left: 0
        background-image: linear-gradient(90deg, var(--mainColor1), var(--mainColor2), var(--mainColor3))
        width: 100%
        height: 25%
        transform: unset
        border-radius: 5px 5px 0 0
        // box-shadow: 0 0 10px rgba(0, 0, 0, 0.6)

    &__arrow_second
        position: absolute
        top: 0
        left: 0
        background-image: linear-gradient(180deg, var(--mainColor1), var(--mainColor2), var(--mainColor3))
        width: 25%
        height: 100%
        transform: unset
        border-radius: 5px 0 0 5px
        // box-shadow: 0 0 10px rgba(0, 0, 0, 0.6)

    &__cancel_first
        position: absolute
        top: 50%
        left: 0
        background-image: linear-gradient(90deg, var(--mainColor3), var(--mainColor2), var(--mainColor1), var(--mainColor2), var(--mainColor3))
        width: 100%
        height: 19%
        transform: unset
        border-radius: 0 5px 0 5px
        transform: translateY(-50%) rotate(-45deg)

    &__cancel_second
        position: absolute
        top: 50%
        left: 0
        background-image: linear-gradient(90deg, var(--mainColor3), var(--mainColor2), var(--mainColor1), var(--mainColor2), var(--mainColor3))
        width: 100%
        height: 19%
        transform: unset
        border-radius: 5px 0 5px 0
        transform: translateY(-50%) rotate(45deg)

    &__wrapper
        width: 100%
        height: 100%
        padding: var(--padding)
        @media (orientation: portrait)
            display: grid
            grid-template-rows: 6vh 1fr
            gap: var(--gapBig)

    &__title
        @include title
        z-index: 1
        @media (orientation: landscape)
            display: none

    &__title_text
        @include title-text
